import axios from 'axios';
const state = {
    data: [],
    total: 0,
    conditions: {
        pageNum: 10,
        pageIndex: 1
    }
};
const actions = {
    orderSearch ({commit}, obj) {
        axios.post('/tableData', Object.assign(state.conditions, obj)).then((res) => {
            console.log(res.data);
            commit('SETDATA', res.data.fData);
            commit('SETTOTAL', res.data.total);
        });
    },
    searchs ({commit}, obj) {
        commit('SEARCHS', obj);
    }
};
const mutations = {
    SETDATA (state, data) {
        state.data = data;
    },
    SETTOTAL (state, total) {
        state.total = total;
    },
    SEARCHS (state, obj) {
        state.conditions.pageNum = obj.pageNum;
    }
};
export default {
    state,
    actions,
    mutations
};



