<template>
    <div class="sel">
        订单状态 <el-select v-model="value" placeholder="请选择" class="selectbox">
            <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
        </el-select>
        收货城市 <el-select v-model="value" placeholder="暂不支持" class="selectbox">
            <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
        </el-select>
        优惠方式 <el-select v-model="value" placeholder="暂不支持" class="selectbox">
            <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
        </el-select>
        下单时间 <el-date-picker
            v-model="value1"
            type="date"
            placeholder="选择日期"
            :picker-options="pickerOptions0" class="selectbox">
        </el-date-picker>至
        <el-date-picker
            v-model="value2"
            type="date"
            placeholder="选择日期"
            :picker-options="pickerOptions0" class="selectbox">
        </el-date-picker>
        发货商家 <el-select v-model="value" placeholder="请选择" class="selectbox">
            <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
            </el-option>
        </el-select>
        <el-form ref="form" :model="form" label-width="80px" style="display: inline-block">
            关键字 <input type="text" class="guanjianzi">
            <el-form-item style="display: inline-block">
                <el-button type="primary" @click="onSubmit">搜索</el-button>
                <el-button>取消</el-button>
        </el-form-item>
        </el-form>
    </div>
</template>
<script>
export default {
    name: 'selectlist',
    data () {
        return {
            options: [{
                value: '选项1',
                label: '黄金糕'
            }, {
                value: '选项2',
                label: '双皮奶'
            }, {
                value: '选项3',
                label: '蚵仔煎'
            }, {
                value: '选项4',
                label: '龙须面'
            }, {
                value: '选项5',
                label: '北京烤鸭'
            }],
            value: '',
            pickerOptions0: {
                disabledDate (time) {
                    return time.getTime() > Date.now();
                }
            },
            value1: '',
            value2: '',
            input: '',
            form: {
                name: ''
            }
        };
    },
    methods: {
        onSubmit () {
            console.log('submit!');
        }
    }
};
</script>
<style>
.sel{
    width: 100%;
    text-align: left;
    box-sizing: border-box;
    padding: 0 10px;
}
.selectbox{
    margin: 10px 5px;
    max-width: 1.8rem;
    max-height: 0.3rem; 
}
.guanjianzi{
    border-radius: 4px;
    border: 1px solid #efefef;
    height: .35rem;
    max-width: 1.8rem;
}
</style>





