<template>
    <el-table
        border
        :data='tableData'
        style="width: 100%">
        <el-table-column
            v-for="(item, k) in tableHead"
            :key="k"
            :label="item[0]"
            :prop='k'
            width="150"
            align='center'
            >
        </el-table-column>
    </el-table>
</template>
<script>
import { mapState, mapActions } from 'vuex';
import { Order } from '@/assets/js/modules.js';
export default {
    name: 'tablelist',
    methods: {
        handleClick (row) {
            console.log(row);
        },
        ...mapActions(['orderSearch'])
    },
    computed: {
        ...mapState({
            tableData: state => state.Order.data
        })
    },
    mounted () {
        this.orderSearch();
    },
    data () {
        return {
            tableHead: Order
        };
    }
};
</script>
<style>
.el-table{
    width: 95% !important;
    margin: 10px auto;
}
.cell{
    text-align: center;
}
</style>





