import axios from 'axios';
import aMock from './data/mock.js';
import orderData from './data/tableData.js';
import Mockadaoter from 'axios-mock-adapter';
export default {
    bootstrap () {
        const mock = new Mockadaoter(axios);
        mock.onPost('/login').reply((config) => {
            return new Promise((resolve, reject) => {
                resolve([200, aMock]);
            });
        });
        mock.onPost('/tableData').reply((config) => {
            console.log(config.data);
            let { pageNum, pageIndex } = JSON.parse(config.data);
            let fData = orderData.slice(pageNum * (pageIndex - 1), pageNum * (pageIndex - 1) + pageNum);
            return new Promise((resolve, reject) => {
                resolve([200, {code: 0, fData, total: orderData.length}]);
            });
        });
    }
};
